.Banner{
    color: white;
    object-fit: contain;
    height: 95vh;
}

.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}
.banner_title{
   font-size: 3 rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}

.banner_button{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);

}

.banner_button:hover{
color: black;
background-color: #e6e6e6;
transition: all 0.2s;
}

.banner_fadeBottom{
    margin-top: 35vh;
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61), 
        #111
    );
/* An RGBA color value is specified with: rgba(red, green, blue, alpha) in our case rgba(dark, dark, dark, alpha). The alpha parameter is a number between 0.0 (fully transparent) and 1.0 (fully opaque). */
}
